							{{scope.row.rizujiage}}
						</template>
					</el-table-column>
					<el-table-column :resizable='true' :sortable='true'  
						prop="zulintianshu"
						label="租赁天数">
						<template slot-scope="scope">
							{{scope.row.zulintianshu}}
						</template>
					</el-table-column>
					<el-table-column :resizable='true' :sortable='true'  
